mergeI18nTranslations(JSON.parse("{\"ar\":{\"close_d634289d\":\"إغلاق\",\"criterion_ratings_3eed22e2\":\"تصنيفات المعيار\",\"learning_outcomes_can_be_included_in_assignment_ru_a48bb903\":\"\\nيمكن تضمين حصائل التعلم في مقاييس تقييم المهمة باعتبارها طريقة سهلة لتقييم\\nحصائل الإجادة المتوائمة مع مهام محددة.  عندما تقوم بتحديد حصيلة\\nتعلم، ينبغي أيضًا تحديد معيار يمكن استخدامه عند بناء\\nمقاييس تقييم المهام.  قم بتحديد أكبر عدد تحتاج إليه من أعمدة مقاييس التقييم، وحدد\\nحد النقاط التي سيتم استخدامها لتحديد إجادة هذه الحصيلة.\",\"more_information_about_ratings_57cdd446\":\"المزيد من المعلومات حول التصنيفات\"},\"cy\":{\"close_d634289d\":\"Cau\",\"criterion_ratings_3eed22e2\":\"Sgoriau maen prawf\",\"learning_outcomes_can_be_included_in_assignment_ru_a48bb903\":\"\\nMae modd cynnwys deilliannau dysgu mewn cyfarwyddiadau sgorio aseiniad fel ffordd hawdd o asesu\\nmeistrolaeth o ddeilliannau sy’n cyd-fynd ag aseiniadau penodol.  Pan fyddwch chi’n diffinio deilliant\\ndysgu, dylech hefyd ddiffinio maen prawf y gellir ei ddefnyddio wrth greu\\ncyfarwyddiadau sgorio ar gyfer aseiniad.  Diffiniwch gynifer o golofnau cyfarwyddyd sgorio ag y bo angen arnoch, a phennu\\ntrothwy pwyntiau a gaiff ei ddefnyddio i ddiffinio meistrolaeth o’r deilliant hwn.\",\"more_information_about_ratings_57cdd446\":\"Rhagor o wybodaeth am sgorau\"},\"da\":{\"close_d634289d\":\"Luk\",\"criterion_ratings_3eed22e2\":\"Kriteriebedømmelse\",\"learning_outcomes_can_be_included_in_assignment_ru_a48bb903\":\"\\nLæringsudbytte kan indgå i opgaverubrikker som en nem måde at vurdere\\nopfyldelse af læringsmål tilpasset til specifikke opgaver.  Når du definerer et\\nlæringsudbytte, skal du også definere et kriterie, der kan bruges ved oprettelse af\\nopgaverubrikker.  Definer så mange rubrikkolonner, som du har brug for, og angiv en\\npointtærskel, der vil blive brugt til at definere opfyldelse af dette læringsmål.\",\"more_information_about_ratings_57cdd446\":\"Flere oplysninger om vurderinger\"},\"da-x-k12\":{\"close_d634289d\":\"Luk\",\"criterion_ratings_3eed22e2\":\"Kriteriebedømmelse\",\"learning_outcomes_can_be_included_in_assignment_ru_a48bb903\":\"\\nLæringsudbytte kan indgå i opgavevurderingskriterier som en nem måde at vurdere\\nopfyldelse af læringsmål tilpasset til specifikke opgaver.  Når du definerer et læringsudbytte,\\nskal du også definere et kriterie, der kan bruges ved oprettelse af opgavevurderingskriterier.\\nDefiner så mange vurderingskriteriekolonner, som du har brug for, og angiv en\\npointtærskel, der vil blive brugt til at definere opfyldelse af dette læringsmål.\",\"more_information_about_ratings_57cdd446\":\"Flere oplysninger om vurderinger\"},\"de\":{\"close_d634289d\":\"Schließen\",\"criterion_ratings_3eed22e2\":\"Kriterienbewertungen\",\"learning_outcomes_can_be_included_in_assignment_ru_a48bb903\":\"\\nLernziele können in das Bewertungsschema für Aufgaben einbezogen werden als einfache Möglichkeit zur Beurteilung der \\nBeherrschung der Lernziele, ausgerichtet an spezifischen Aufgaben.  Beim Definieren eines Lernziels \\nsollten Sie auch ein Kriterium definieren, das für das Einrichten des \\nBewertungsschemas für die Aufgabe verwendet werden kann.  Definieren Sie so viele Bewertungsschemaspalten, wie Sie benötigen, und geben Sie die \\nPunktzahl an, die benötigt wird, um das Lernziel zu erreichen.\",\"more_information_about_ratings_57cdd446\":\"Weitere Informationen über Bewertungen\"},\"el\":{\"close_d634289d\":\"Κλείσιμο\",\"criterion_ratings_3eed22e2\":\"Κατάταξη Βάσει Κριτηρίων\"},\"en-AU\":{\"close_d634289d\":\"Close\",\"criterion_ratings_3eed22e2\":\"Criterion Ratings\",\"learning_outcomes_can_be_included_in_assignment_ru_a48bb903\":\"\\nLearning outcomes can be included in assignment rubrics as an easy way to assess\\nmastery of outcomes aligned to specific assignments.  When you define a learning\\noutcome, you should also define a criterion that can be used when building\\nassignment rubrics.  Define as many rubric columns as you need and specify a\\npoint threshold that will be used to define mastery of this outcome.\",\"more_information_about_ratings_57cdd446\":\"More Information About Ratings\"},\"en-CA\":{\"close_d634289d\":\"Close\",\"criterion_ratings_3eed22e2\":\"Criterion Ratings\",\"learning_outcomes_can_be_included_in_assignment_ru_a48bb903\":\"\\nLearning outcomes can be included in assignment rubrics as an easy way to assess\\nmastery of outcomes aligned to specific assignments.  When you define a learning\\noutcome, you should also define a criterion that can be used when building\\nassignment rubrics.  Define as many rubric columns as you need, and specify a\\npoint threshold that will be used to define mastery of this outcome.\\n\",\"more_information_about_ratings_57cdd446\":\"More Information About Ratings\"},\"en-GB\":{\"close_d634289d\":\"Close\",\"criterion_ratings_3eed22e2\":\"Criterion ratings\",\"learning_outcomes_can_be_included_in_assignment_ru_a48bb903\":\"\\nLearning outcomes can be included in assignment rubrics as an easy way to assess\\nmastery of outcomes aligned to specific assignments.  When you define a learning\\noutcome, you should also define a criterion that can be used when building\\nassignment rubrics.  Define as many rubric columns as you need and specify a\\npoint threshold that will be used to define mastery of this outcome.\",\"more_information_about_ratings_57cdd446\":\"More Information About Ratings\"},\"es\":{\"close_d634289d\":\"Cerrar\",\"criterion_ratings_3eed22e2\":\"Clasificaciones del criterio\",\"learning_outcomes_can_be_included_in_assignment_ru_a48bb903\":\"\\nLos resultados del aprendizaje pueden incluirse en rúbricas de tareas como una forma de evaluar fácilmente\\nel dominio de los resultados vinculados con tareas específicas.  Cuando defina un resultado\\ndel aprendizaje, también deberá establecer un criterio que pueda usarse al momento de elaborar\\nrúbricas de tareas.  Defina tantas columnas de rúbricas como necesite y especifique un umbral de\\npunto(s) que se utilizará para definir el dominio sobre esta competencia.\\n\",\"more_information_about_ratings_57cdd446\":\"Más información sobre las calificaciones\"},\"fa\":{\"close_d634289d\":\"بستن\",\"criterion_ratings_3eed22e2\":\"رتبه بندی های معیار\"},\"fi\":{\"close_d634289d\":\"Sulje\",\"criterion_ratings_3eed22e2\":\"Kriteerien luokitus\",\"learning_outcomes_can_be_included_in_assignment_ru_a48bb903\":\"\\nOppimisen tulokset voidaan sisällyttää tehtävän rubriikkeihin helppona tapana arvioida\\nmäärättyihin tehtäviin kohdistettujen tulosten hallintaa.  Kun määrität oppimisen\\ntuloksen, sinun pitäisi myös määrittää kriteeri, jotka voidaan käyttää, kun rakennetaan\\ntehtävän rubriikkeja.  Määritä tarvitsemasi määrä rubriikin sarakkeita, ja määritä\\npisteen kynnys, jota käytetään tämän tuloksen hallinnan määrittämisessä.\",\"more_information_about_ratings_57cdd446\":\"Lisätietoja arvosteluista\"},\"fr\":{\"close_d634289d\":\"Fermer\",\"criterion_ratings_3eed22e2\":\"Notes du critère\",\"learning_outcomes_can_be_included_in_assignment_ru_a48bb903\":\"\\nLes résultats d'apprentissage peuvent être inclus dans les barèmes des travaux comme un moyen facile d'évaluer\\nla maîtrise des résultats alignés sur des travaux spécifiques.  Lorsque vous définissez un résultat\\nd'apprentissage, vous devez également définir un critère qui peut être utilisé lors de l'élaboration\\ndes barèmes des travaux.  Définissez autant de colonnes de barèmes que nécessaire, et spécifiez un\\nseuil de points qui sera utilisé pour définir la maîtrise de ce résultat.\",\"more_information_about_ratings_57cdd446\":\"Plus d’informations sur les notations\"},\"fr-CA\":{\"close_d634289d\":\"Fermer\",\"criterion_ratings_3eed22e2\":\"Notes du critère\",\"learning_outcomes_can_be_included_in_assignment_ru_a48bb903\":\"\\nLes acquis peuvent être inclus dans la rubrique des tâches comme un moyen facile d'évaluer\\nla maîtrise des résultats correspondant à des tâches précises.  Lorsque vous définissez un acquis\\n, vous devez également définir un critère qui peut être utilisé lors de la création des\\nrubriques des tâches.  Définir autant de colonnes de rubrique dont vous avez besoin, et spécifier\\nun seuil de points qui sera utilisé pour définir la maîtrise de ce résultat.\",\"more_information_about_ratings_57cdd446\":\"De plus amples renseignements au sujet de notes\"},\"he\":{\"close_d634289d\":\"סגירה\",\"criterion_ratings_3eed22e2\":\"דירוגי קריטריון \"},\"ht\":{\"close_d634289d\":\"Fèmen\",\"criterion_ratings_3eed22e2\":\"Kritè Evalyasyon\",\"learning_outcomes_can_be_included_in_assignment_ru_a48bb903\":\"\\nRezilta aprantisaj yo kapab enkli nan ribrik afektasyon yo kòm mwayen fasil\\npou ka evalyemetriz rezilta ki koresponn ak sesyon espesyal.  Lè w ap defini\\nyon reszilta aprantisaj, ou ta dwe defini yon kritè ki kapab itilize nan\\nelaborasyon ribrik sesyon yo.  Defini kantite kolòn ribrik ki nesesè epi \\nmete yon limit pwen ke y ap itilize pou defini metriz rezilta sa a.\",\"more_information_about_ratings_57cdd446\":\"Plis Enfòmasyon sou Klasman yo\"},\"hu\":{\"close_d634289d\":\"Bezárás\",\"criterion_ratings_3eed22e2\":\"Kritériumértékelések\"},\"hy\":{\"close_d634289d\":\"Փակել\",\"criterion_ratings_3eed22e2\":\"Վարկանիշերի չափանիշը\"},\"is\":{\"close_d634289d\":\"Loka\",\"criterion_ratings_3eed22e2\":\"Viðmiðunarmat\",\"learning_outcomes_can_be_included_in_assignment_ru_a48bb903\":\"\\nHægt er að hafa lærdómsniðurstöður með í matsviðmiðum verkefnis sem auðvelda leið til að\\nleggja mat á leiknistig niðurstaðna sem er samstillt við sérstök verkefni.  Þegar þú\\nskilgreinir lærdóms niðurstöðu, ættir þú líka að skilgreina viðmið sem hægt er að nota þegar\\nmatsviðmið verkefnis eru gerð.  Skilgreindu jafnmarga matsviðmiðadálka og þú þarft og tiltaktu\\nstigaþröskuld sem verður notaður til að skilgreina leiknistig þessarar niðurstöðu.\",\"more_information_about_ratings_57cdd446\":\"Frekari upplýsingar um einkunnir\"},\"it\":{\"close_d634289d\":\"Chiudi\",\"criterion_ratings_3eed22e2\":\"Valutazioni basate su criterio\",\"learning_outcomes_can_be_included_in_assignment_ru_a48bb903\":\"\\nI risultati dell’apprendimento possono essere inclusi nelle rubriche dei compiti come semplice modo per valutare la padronanza\\ndei risultati allineata a compiti specifici.  Quando si definisce un risultato\\ndi apprendimento, è necessario definire anche un criterio da utilizzare per la creazione\\ndelle rubriche dei compiti.  Definire tutte le colonne rubrica di cui si ha bisogno e specificare una soglia del\\npunto che sarà utilizzata per definire la padronanza di questo risultato.\",\"more_information_about_ratings_57cdd446\":\"Maggiori informazioni sulle valutazioni\"},\"ja\":{\"close_d634289d\":\"閉じる\",\"criterion_ratings_3eed22e2\":\"基準の評価\",\"learning_outcomes_can_be_included_in_assignment_ru_a48bb903\":\"\\n学習成果は、特定の課題に合わせた成果の習熟を評価する簡単な方法として、\\n課題ルーブリックに含めることができます。学習成果を定義する際には、課題ル\\nーブリックビルド時に使用できる基準も定義しなければなりません。必要なだけ\\nの数のルーブリック列を定義し、この成果の習得を定義するために使用される\\nポイントの閾値を指定します。\",\"more_information_about_ratings_57cdd446\":\"格付け評価に関する詳細情報\"},\"ko\":{\"close_d634289d\":\"닫기\",\"criterion_ratings_3eed22e2\":\"기준 등급\"},\"mi\":{\"close_d634289d\":\"Katia\",\"criterion_ratings_3eed22e2\":\"Whakatauranga paearu\",\"learning_outcomes_can_be_included_in_assignment_ru_a48bb903\":\"\\nKo ngā putanga ako ka taea te whakauru ki ngā whakataunga rubrics hei huarahi ngāwari ki te aromatawai\\nmana o ngā putanga e hāngai ana ki ngā whakataunga motuhake.  Ka whakatau koe i te putanga o te ako,\\nme whakamārama anō hoki koe i tētahi tohu ka taea te whakamahi i te hanganga o ngā whakataunga rubrics.\\nTautuhia te maha o ngā tīwae rubric e hiahiatia ana e koe, me tautuhi i te pae e whakamahia ana hei\\ntautuhi i te mana o tēnei putanga.\",\"more_information_about_ratings_57cdd446\":\"He Korero Ano Mo Ngā Ratonga\"},\"nb\":{\"close_d634289d\":\"Lukk\",\"criterion_ratings_3eed22e2\":\"Kriterievurderinger\",\"learning_outcomes_can_be_included_in_assignment_ru_a48bb903\":\"\\nLæringsresultat kan inkluderes i oppgaverubrikkene som en enkle måte å vurdere\\nekspertise av resultat justert til bestemte oppgaver.  Når du definerer et lærings\\nresultat, må du også definere et kriterie som kan brukes fore å bygge oppgaverubrikker.\\nDefiner så mange rubrikk-kolonner som du trenger, og spesifiser poeng-terskler som\\nskal brukes for å definere ekspertisen til dette læringsmålet.\",\"more_information_about_ratings_57cdd446\":\"Mer informasjon om rangeringer\"},\"nb-x-k12\":{\"close_d634289d\":\"Lukk\",\"criterion_ratings_3eed22e2\":\"Kriterievurderinger\",\"learning_outcomes_can_be_included_in_assignment_ru_a48bb903\":\"\\nLæringsresultat kan inkluderes i oppgavevurderingskriterier som en enkle måte å vurdere\\nekspertise av resultat justert til bestemte oppgaver.  Når du definerer et lærings\\nresultat, må du også definere et kriterie som kan brukes fore å bygge\\noppgavevurderingskriterier.  Definer så mange kolonner for vurderingskriterier som du trenger, og spesifiser\\npoeng-terskler som skal brukes for å definere ekspertisen til dette målet.\",\"more_information_about_ratings_57cdd446\":\"Mer informasjon om rangeringer\"},\"nl\":{\"close_d634289d\":\"Sluiten\",\"criterion_ratings_3eed22e2\":\"Criterium voor beoordelingen\",\"learning_outcomes_can_be_included_in_assignment_ru_a48bb903\":\"\\nLeerdoelen kunnen in opdrachtrubrieken worden opgenomen om op een eenvoudige manier studieresultaten\\nte koppelen aan specifieke opdrachten.  Wanneer je een leerdoel definieert, moet je ook een criterium\\ndefiniëren dat kan worden gebruikt bij het samenstellen van opdrachtrubrieken.  Definieer zoveel\\nrubriekkolommen als je nodig hebt en geef het aantal punten op dat minimaal behaald moet worden\\nom dit leerdoel te behalen.\",\"more_information_about_ratings_57cdd446\":\"Meer informatie over scores\"},\"nn\":{\"close_d634289d\":\"Lukk\",\"criterion_ratings_3eed22e2\":\"Kriterievurderingar\",\"learning_outcomes_can_be_included_in_assignment_ru_a48bb903\":\"\\n  Læringsmål kan inkluderast i vurderingsskjema for oppgåver, slik at du på ein enkel måte kan vurdere\\n  meistringsnivå i bestemte oppgåver. Når du definerer eit læringsmål,\\n  bør du også definere eit kriterium som kan brukast når du lagar\\n  vurderingskriterium for oppgåver. Opprett dei rubrikkane du treng og set ein\\n  poengterskel for å definere meistringa av læringsmålet.\\n\",\"more_information_about_ratings_57cdd446\":\"Meir informasjon om nivå\"},\"pl\":{\"close_d634289d\":\"Zamknij\",\"criterion_ratings_3eed22e2\":\"Punktacja dla danego kryterium\",\"learning_outcomes_can_be_included_in_assignment_ru_a48bb903\":\"\\nWyniki nauki można uwzględnić w kryteriach ocen dla zadań jako łatwy sposób\\nokreślenia stopnia opanowania wyników dla określonych testów.  Gdy definiowany\\njest wynik nauki, należy także podać kryterium używane do tworzenia kryteriów\\nocen testu.  Zdefiniuj tyle kolumn kryteriów ocen, ile potrzeba, oraz określ\\npunkt graniczny używany do zdefiniowania stopnia opanowania takich wyników.\",\"more_information_about_ratings_57cdd446\":\"Więcej informacji o ocenach\"},\"pt\":{\"close_d634289d\":\"Fechar\",\"criterion_ratings_3eed22e2\":\"Classificações do critério\",\"learning_outcomes_can_be_included_in_assignment_ru_a48bb903\":\"\\nOs resultados de aprendizagem podem ser incluídos nas rubricas de atribuição como uma maneira fácil de\\navaliar o domínio dos resultados alinhados a atribuições específicas.  Ao definir um resultado de\\naprendizado, você também deve definir um critério que possa ser usado ao criar rubricas de\\natribuição.  Defina quantas colunas de rubricas forem necessárias e especifique um\\nlimiar de limite que será usado para definir o domínio desse resultado.\",\"more_information_about_ratings_57cdd446\":\"Mais informações sobre avaliações\"},\"pt-BR\":{\"close_d634289d\":\"Fechar\",\"criterion_ratings_3eed22e2\":\"Avaliações do critério\",\"learning_outcomes_can_be_included_in_assignment_ru_a48bb903\":\"\\nObjetivos de aprendizagem podem ser incluídos em rubricas de tarefas como uma forma fácil\\nde avaliar o domínio dos objetivos alinhados a tarefas especificas.  Ao definir um resultado\\ndo aprendizado, você também deve definir um critério que possa ser usado ao fazer rubricas\\nda tarefa.  Defina quantas colunas da rubrica desejar, e especifique um limite de pontos\\nque será usado para definir o domínio deste objetivo.\",\"more_information_about_ratings_57cdd446\":\"Mais informações sobre avaliações\"},\"ru\":{\"close_d634289d\":\"Закрыть\",\"criterion_ratings_3eed22e2\":\"Рейтинги критерия\",\"learning_outcomes_can_be_included_in_assignment_ru_a48bb903\":\"\\nРезультаты обучения могут быть включены в рубрики заданий как простой способ оценивания\\nуровня знаний на основе сравнения со специальными заданиями.  После определения результатов\\nобучения вам необходимо задать критерий, который можно использовать при создании\\nрубрик заданий.  Задайте необходимое количество столбцов рубрик и установите\\nзначение порога, которое будет использоваться для определения уровня знаний по данной рубрике.\",\"more_information_about_ratings_57cdd446\":\"Более подробная информация о рейтингах\"},\"sl\":{\"close_d634289d\":\"Zapri\",\"criterion_ratings_3eed22e2\":\"Rezultati po merilu\",\"learning_outcomes_can_be_included_in_assignment_ru_a48bb903\":\"\\nVključevanje učnih ciljev v ocenjevalne vodnike naloge je enostaven način za oceno odličnosti učnih izidov,\\nporavnanih s specifičnimi nalogami.  Ko določite učni cilj, morate\\ndoločiti tudi merilo, ki ga je mogoče uporabiti pri pripravi ocenjevalnih\\nvodnikov za nalogo.  Določite toliko stolpcev ocenjevalnega vodnika, kot želite, in določite\\ntočko praga, ki bo pri tem izidu določala odličnost.\\n\",\"more_information_about_ratings_57cdd446\":\"Več informacij o ocenah\"},\"sv\":{\"close_d634289d\":\"Stäng\",\"criterion_ratings_3eed22e2\":\"Poängsättning av kriterier\",\"learning_outcomes_can_be_included_in_assignment_ru_a48bb903\":\"\\nLärandemål kan inkluderas i uppgiftsmatriser som ett enkelt sätt att utvärdera\\nuppfyllelse av lärandemål associerade med enskilda uppgifter.  När du definierar ett\\nlärandemål, bör du även definiera ett kriterium som kan användas för att skapa\\nuppgiftsmatriser.  Definiera så många matriskolumner du behöver, och ange en\\npoängtröskel som definierar uppfyllande av det här lärandemålet.\\n\",\"more_information_about_ratings_57cdd446\":\"Mer information om värderingar: \"},\"sv-x-k12\":{\"close_d634289d\":\"Stäng\",\"criterion_ratings_3eed22e2\":\"Poängsättning av kriterier\",\"learning_outcomes_can_be_included_in_assignment_ru_a48bb903\":\"\\nLärandemål kan inkluderas i uppgiftsmatriser som ett enkelt sätt att utvärdera\\nuppfyllelse av lärandemål associerade med enskilda uppgifter.  När du definierar ett\\nlärandemål, bör du även definiera ett kriterium som kan användas för att skapa\\nuppgiftsmatriser.  Definiera så många matriskolumner du behöver, och ange en\\npoängtröskel som definierar uppfyllande av det här lärandemålet.\\n\",\"more_information_about_ratings_57cdd446\":\"Mer information om värderingar: \"},\"tr\":{\"close_d634289d\":\"Kapat\",\"criterion_ratings_3eed22e2\":\"Kriter Oranları\",\"learning_outcomes_can_be_included_in_assignment_ru_a48bb903\":\"\\n  Öğrenme çıktıları belirli ödevler için belirlenmiş çıktıların yeterliliğini kolay bir şekilde\\n  değerlendirme amacıyla ödev değerlendirme listelerinde kullanılabilir. Bir öğrenme\\n  çıktısı tanımladığınızda ödev değerlendirme listelerini oluşturmada kullanılabilecek\\n  bir kriter de tanımlamalısınız. İstediğiniz kadar değerlendirme listesi sütunu oluşturup bu\\n  öğrenme çıktısının yeterliliğini belirleme kullanılabilecek eşik puan değeri belirleyin.\\n\"},\"uk\":{\"close_d634289d\":\"Закрити\"},\"zh-Hans\":{\"close_d634289d\":\"关闭\",\"criterion_ratings_3eed22e2\":\"标准等级\",\"learning_outcomes_can_be_included_in_assignment_ru_a48bb903\":\"\\n学习结果可以加入作业品格表格中，便于轻松评估\\n与特定作业一致的结果的掌握程度。当您定义学习\\n结果时，您还应该定义在构建\\n作业品评价量规时可以使用的标准。定义您所需数量的评价量规栏，并指定\\n定义此结果掌握程度时要使用的分数阈值。\",\"more_information_about_ratings_57cdd446\":\"关于评分等级的更多信息\"},\"zh-Hant\":{\"close_d634289d\":\"關閉\",\"criterion_ratings_3eed22e2\":\"標準評級\",\"learning_outcomes_can_be_included_in_assignment_ru_a48bb903\":\"\\n學習成果可以包括在作業評分標準中，以作為一種簡單的方法，\\n來評估針對特定作業成果的掌握程度。當您定義學習\\n成果時，您也應該定義可以在創建\\n作業評分標準時使用的標準。根據需要定義評分標準欄，並指定\\n將被用於定義此成果的掌握程度的臨界分數。\",\"more_information_about_ratings_57cdd446\":\"更多有關評分的資訊\"}}"));
